//components
import React, { Component } from "react";
import { CardList } from "./components/card-list/card-list.component";
import { SearchBox } from "./components/search-box/search-box.component";

//pokemon data
import pokemonData from "./data/pokemon";

//Stylesheet
import "./App.css";

class App extends Component {
  constructor() {
    super();
    this.state = {
      pokemon: {},
      searchField: ""
    };
  }

  componentDidMount() {
    this.setState({ pokemon: pokemonData });
  }
  handleChange = e => {
    this.setState({ searchField: e.target.value });
  };

  render() {
    const { pokemon, searchField } = this.state;

    const filteredPokemon = Object.keys(pokemon).filter(pokemon =>
      pokemon.toLowerCase().includes(searchField.toLowerCase())
    );
    
    return (
      <div className="App">
        <a id="title" href="https://fontmeme.com/pokemon-font/"><img src="https://fontmeme.com/permalink/190916/0a7514d944b35090004e6a1ee3c5fd2a.png" alt="pokemon-font" border="0"/></a>
        <SearchBox
          placeholder="search for Pokemon"
          handleChange={this.handleChange}
        />
        <CardList pokemon={filteredPokemon} data={pokemon}/>
      </div>
    );
  }
}

export default App;
