//components
import React, { Component } from "react";

//stylesheet
import "./card.styles.css";

//dependencies
const Pokedex = require("pokeapi-js-wrapper");
const P = new Pokedex.Pokedex();

class Card extends Component {
  constructor(props) {
    super(props);
    this.handleClick = this.handleClick.bind(this);
    this.state = {
      pokemonName: "",
      clicked: false,
      height: "",
      weight: "",
      type: ""
    };
  }
  componentDidMount() {
    this.setState({ pokemonName: this.props.name });
  }

  handleClick() {
    this.setState({ clicked: !this.state.clicked });
    P.getPokemonByName(this.state.pokemonName.toLowerCase()).then(res => {
      console.log(res);
      const typeData = [];
      res.types.forEach(function(e) {
        typeData.push(e.type.name);
      });
      this.setState({height: res.height, weight: res.weight, type: typeData})
    });
  }

  render() {
    let displayData;
    if (this.state.clicked) {
      displayData = (
        <div>
          <p>{`Height: ${this.state.height} Weight: ${this.state.weight}`}</p>
          <p>{`Types: ${this.state.type}`}</p>
        </div>
      );
    } else {
      displayData = "";
    }
    return (
      <div className="card-container" onClick={this.handleClick}>
        <img className="card-img" alt={``} src={this.props.img} />
        <h2>{this.props.name}</h2>
        {displayData}
      </div>
    );
  }
}

export default Card;
